/* ## Botones
--------------------------------------------- */
.btn-kokomo {
  display: inline-block;
  font-weight: 400;
  text-align: center;
  vertical-align: middle;
  user-select: none;
  padding: 0.375rem 0.75rem;
  font-size: 1rem;
  line-height: 1.5;
  border: none;
  border-radius: 5px;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out,
    border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}

.btn-kokomo-success {
  color: #fff;
  background-color: #0ac5a3;
  border-color: #0ac5a3;
}
.btn-kokomo-success:hover {
  color: #fff;
  background-color: #0ac09f;
  border-color: #0ac09f;
}

.btn-kokomo-grey {
  color: #fff;
  background-color: #c9c0b6;
  border-color: #c9c0b6;
}
.btn-kokomo-grey:hover {
  color: #fff;
  background-color: #c7bdb3;
  border-color: #c7bdb3;
}

.btn-kokomo-danger {
  color: #fff;
  background-color: #ff8d69;
  border-color: #ff8d69;
}
.btn-kokomo-danger:hover {
  color: #fff;
  background-color: #ff8964;
  border-color: #ff8964;
}

.btn-kokomo-white {
  color: #ffba69;
  background-color: white;
  border-color: white;
}
.btn-kokomo-white:hover {
  color: #ffba69;
  background-color: #fcfcfc;
  border-color: #fcfcfc;
  transition: 0.6s all ease;
}

.btn-kokomo-google {
