/* # 01. MEDIA QUERIES
---------------------------------------------------------------------------------------------------- */
/* ## MEDIA QUERIES PANTALLAS +768px
--------------------------------------------- */
@media all and (min-width: 768px) {
  .body-container {
    margin-left: 140px;
    padding-bottom: 0px;
  }
  .shadow-blue {
    padding: 30px;
    box-shadow: 0px 0px 30px rgba(50, 148, 187, 0.28);
    border-radius: 20px;
  }
  .hero-landing {
    padding: 10px;
    background-image: url(/images/hero.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    min-height: 80vh;
    background-position: top center;
    margin-top: 50px;
  }
  .hero-landing .home-container {
    padding: 0px;
  }
  .hero-landing .home-container .hero-title {
    font-size: 32px;
  }
  .home-container {
    margin-left: 140px;
    padding-bottom: 0px;
  }
  .landing-container {
    margin-left: 40px;
    padding-bottom: 0px;
  }
  .hero {
    min-width: 20vw;
  }
  .home-bg {
    display: flex;
    align-items: center;
    min-height: 100vh;
  }
  .hero-arrow-phone {
    display: none;
  }
  .hero-arrow-desktop {
    display: block;
    font-size: 35px;
    margin-left: -6px;
    color: #d0dde2;
    margin-top: 10px;
  }
  .navbar-kokomo {
    box-shadow: 0px 4px 40px rgba(141, 136, 131, 0.45);
    border-radius: 26px;
    position: fixed;
    top: 0;
    right: auto;
    width: auto;
    margin: 10px;
